  text-transform: capitalize;
  color: #181c51;
  padding: 15px;
  position: relative;
  font-size: 16px;
}

.offcanvas-menu ul li.active > a,
.offcanvas-menu ul li a:hover {
  background: #fff;
}

.offcanvas-menu a:not(:only-child):after {
  content: "\ea99";
  font-family: "IcoFont";
  position: absolute;
  right: 10px;
  top: 15px;
}

.offcanvas-menu .active > a:not(:only-child):after {
