@media screen and (min-width: 1200px) {
  .sub-menu {
    left: 0;
  }
}

.sub-menu li + li {
  margin-left: 0px !important;
}

.sub-menu .sub-menu-link {
  font-family: "Metropolis Regular";
  text-align: left;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.375;
  display: block;
  padding: 8px 30px;
}

.main-menu li:hover .sub-menu {
  visibility: visible;
  margin-top: 15px;
  opacity: 1;
}

