 # offcanvas menu style
 ********************************/
/*------------------------------------- 
 # Offcanvas Humbergar Menu Style Start
 -------------------------------------*/
.toggle {
  margin-top: 10px;
}

@media screen and (min-width: 992px) {
  .toggle {
    display: none;
    outline: 0;
  }
}

.toggle span {
  position: relative;
  display: block;
  overflow: hidden;
  width: 24px;
  height: 2px;
}

.toggle span + span {
  margin-top: 6px;
}

.toggle span::after,
.toggle span::before {
  position: absolute;
  top: 0;
  left: 0;
  flex: 1 0 100%;
  width: 100%;
  height: 100%;
  content: "";
  background-color: #263238;
}

.toggle span.icon-bottom::before,
.toggle span.icon-top::before {
  transition: transform 0.6s cubic-bezier(0.165, 0.84, 0.44, 1) 0.2s;
  transform: scaleX(1) translateZ(0);
  transform-origin: right;
}

.toggle span.icon-bottom::after,
.toggle span.icon-top::after {
  transition: transform 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
  transform: scaleX(0) translateZ(0);
  transform-origin: left;
}

.toggle span.icon-middle::before {
  transition: transform 0.6s cubic-bezier(0.165, 0.84, 0.44, 1) 0.2s;
  transform: scaleX(1) translateZ(0);
  transform-origin: left;
}

.toggle span.icon-middle::after {
  transition: transform 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
  transform: scaleX(0) translateZ(0);
  transform-origin: right;
}

.toggle:hover span::after,
.toggle:hover span::before {
  background-color: #59e3a7;
}

.toggle:hover span.icon-bottom::before,
.toggle:hover span.icon-top::before {
  transition: transform 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
  transform: scaleX(0) translateZ(0);
}

.toggle:hover span.icon-bottom::after,
.toggle:hover span.icon-top::after {
  transition: transform 0.6s cubic-bezier(0.165, 0.84, 0.44, 1) 0.2s;
  transform: scaleX(1) translateZ(0);
}

.toggle:hover span.icon-middle::before {
  transition: transform 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
  transform: scaleX(0) translateZ(0);
}

.toggle:hover span.icon-middle::after {
  transition: transform 0.6s cubic-bezier(0.165, 0.84, 0.44, 1) 0.2s;
  transform: scaleX(1) translateZ(0);
}

/*------------------------------------- 
 # Offcanvas Humbergar Menu Style ENd
 -------------------------------------*/
.offcanvas-modal.modal.fade .modal-dialog {
  transform: translateX(100%);
}

.offcanvas-modal.modal.show .modal-dialog {
  transform: translateX(0);
}

.offcanvas-dialog {
  position: absolute;
  top: 0;
  right: 0;
  width: 300px;
  height: 100%;
  margin: 0;
}

.offcanvas-dialog .modal-content {
  height: 100%;
  border-radius: 0;
  border: 0;
}

.offcanvas-header {
  padding: 20px 15px;
  border-bottom: 1px solid #cccccc;
}

.offcanvas-logo {
  max-width: 160px;
}

.offcanvas-menu {
  position: relative;
  z-index: 1;
  height: 100%;
}

.offcanvas-menu::before {
  position: absolute;
  z-index: -1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  content: "";
  background-color: #fff;
}

.offcanvas-menu ul ul {
  display: none;
}

.offcanvas-menu > ul > li > ul li {
  border-bottom: 0px;
}

.offcanvas-menu > ul > li > ul li a {
  font-family: "Metropolis Regular", sans-serif;
  border-top: 1px solid #cccccc;
  padding-left: 30px;
}

.offcanvas-menu ul > li {
  border-bottom: 1px solid #cccccc;
}

.offcanvas-menu li.active > ul {
  display: block;
}

.offcanvas-menu li a {
  font-family: "Metropolis Semi Bold";
  display: block;
  text-transform: capitalize;
  color: #181c51;
  padding: 15px;
  position: relative;
  font-size: 16px;
}

.offcanvas-menu ul li.active > a,
.offcanvas-menu ul li a:hover {
  background: #fff;
}

.offcanvas-menu a:not(:only-child):after {
  content: "\ea99";
  font-family: "IcoFont";
  position: absolute;
  right: 10px;
  top: 15px;
}

.offcanvas-menu .active > a:not(:only-child):after {
  content: "\eab9";
}

/******************************** 
# Offcanvas Menu Style End
********************************/
